/**
 * @module patterns/finder/components/cards/finder__results__course
 * @author Web Development
 * @copyright City, University of London 2019
 */

import PropTypes from 'prop-types';
import React from 'react';
import formatLabel from '../results/formatLabel';
import { uppercaseFirstLetterLowercaseRest } from '../../../../util';

/**
 * Render a Funnelback result as a course card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Course(props) {
    const subtitle =
            (props.details.listMetadata.type && props.details.listMetadata.type[0]) || 
            (props.details.listMetadata.level && props.details.listMetadata.level[0]) || null,
        award = props.details.listMetadata.qualification && (
            <p className="card__tag">
                <span className="fas fa-fw fa-award icon" aria-hidden="true" />{' '}
                <span>
                    Award
                    {props.details.listMetadata.qualification.length >
                        1 && 's'}
                    :
                </span>{' '}
                <span>
                    {props.details.listMetadata.qualification.join(', ')}
                </span>
            </p>
        ),
        duration = props.details.listMetadata.duration && (
            <p className="card__tag">
                <span className="fal fa-fw fa-clock icon" aria-hidden="true" />{' '}
                <span>Duration:</span>{' '}
                <span>{props.details.listMetadata.duration[0]}</span>
            </p>
        ),
        method = props.details.listMetadata.method &&
            props.details.listMetadata.level[0] !==
                'Short courses and professional development' &&
            ((props.query.facets && props.query.facets.method) ||
                props.query.query.indexOf(props.details.listMetadata.method[0]) >= 0 ||
                props.details.listMetadata.method[0].indexOf('Online') >= 0) && (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-book-reader icon"
                        aria-hidden="true"
                    />{' '}
                    <span>
                        Method
                        {props.details.listMetadata.method.length > 1 &&
                            's'}{' '}
                        of study:
                    </span>{' '}
                    <span>
                        {uppercaseFirstLetterLowercaseRest(
                            props.details.listMetadata.method.join('|')
                        )
                            .split('|')
                            .join(', ')}
                    </span>
                </p>
            ),
        location = props.details.listMetadata.location &&
            props.query.facets &&
            props.query.facets.location && (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-map-marker-alt icon"
                        aria-hidden="true"
                    />{' '}
                    <span>Location:</span>{' '}
                    <span>
                        {/* Avoid duplication of 'Online' location label if location and method of study are both online */}
                        {props.details.listMetadata.location.join('|') === 'Online|Online'
                            ? 'Online'
                            : props.details.listMetadata.location.join(', ')}
                    </span>
                </p>
            ),
        school = props.details.listMetadata.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>{props.details.listMetadata.school.map((e,i) => (
                    i === 0 ? 
                    <React.Fragment key={i}>
                        {e}
                    </React.Fragment>
                    : 
                    <React.Fragment key={i}>
                         | {e}
                    </React.Fragment>
                ))}</span>
            </p>
        ),
        // City and Business School use different metadata values; check for both
        clearing = props.details.listMetadata.clearing && ['Yes', 'yes'].includes(props.details.listMetadata.clearing[0]) && (
            <div className="card__label card--course__label--clearing">
                <p className="card__label card--course__label--clearing">
                    Clearing
                </p>
            </div>
        ),
        external = props.details.indexUrl.indexOf('bayes.city.ac.uk') >= 0 && (
            <>
                {' '}
                <span
                    className="fas fa-external-link icon"
                    aria-label="(external link)"
                ></span>
            </>
        ),
        courseCode =
            props.details.listMetadata.code &&
            ([
                'Undergraduate degree',
                'Foundation course',
                'Postgraduate taught degree',
            ].some((s) => props.details.listMetadata.level.indexOf(s) >= 0) ||
                props.query.query.indexOf(props.details.listMetadata.code) >= 0) ? (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-graduation-cap icon"
                        aria-hidden="true"
                    />{' '}
                    <span>
                        Course code
                        {props.details.listMetadata.code.length > 1 &&
                            's'}
                        :
                    </span>{' '}
                    <span>
                        {props.details.listMetadata.code.join('/')}
                    </span>
                </p>
            ) : null;

    return (
        <li className="card card--course">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                <div className="wrapper--card__label__details">
                    {clearing}
                    <div className="card__details__text">
                        <h3 className="card__heading">
                            {props.query.query
                                ? formatLabel(
                                      props.details.title,
                                      props.query.query
                                  )
                                : props.details.title}
                            {external}
                        </h3>
                        <p className="card__type">
                            {props.query.query
                                ? formatLabel(subtitle, props.query.query)
                                : subtitle && subtitle}
                        </p>
                        <p className="card__description">
                            {props.query.query
                                ? formatLabel(
                                      props.details.listMetadata.c[0],
                                      props.query.query
                                  )
                                : props.details.listMetadata.c &&
                                  props.details.listMetadata.c[0]}
                        </p>
                        {school}
                        {award}
                        {duration}
                        {courseCode}
                        {method}
                        {location}
                    </div>
                </div>
            </a>
        </li>
    );
}

Finder__Results__Course.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
};

export default Finder__Results__Course;
