/**
 * @module patterns/finder/components/cards/finder__results__contact
 * @author Web Development
 * @copyright City, University of London 2019
 */

import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a contact card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Contact(props) {
    const department = props.details.listMetadata.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span> {props.details.listMetadata.department[0]}
            </p>
        ),
        school = props.details.listMetadata.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span> {props.details.listMetadata.school[0]}
            </p>
        ),
        jobtitle = props.details.listMetadata.jobtitle && (
            <p className="card__type">{props.details.listMetadata.jobtitle[0]}</p>
        ),
        email = props.details.listMetadata.email && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-envelope icon"
                    aria-hidden="true"
                />{' '}
                <span>Email:</span>{' '}
                <a href={'mailto:' + props.details.listMetadata.email[0]}>
                    {props.details.listMetadata.email[0]}
                </a>
            </p>
        ),
        telephone = props.details.listMetadata.telephone && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-phone fa-rotate-90 icon"
                    aria-hidden="true"
                />{' '}
                <span>Telephone:</span>{' '}
                <a href={'tel:' + props.details.listMetadata.telephone[0]}>
                    {props.details.listMetadata.friendlytelephone[0]}
                </a>
            </p>
        ),
        room = props.details.listMetadata.room && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-door-open icon"
                    aria-hidden="true"
                />{' '}
                <span>Room:</span> {props.details.listMetadata.room[0]}
            </p>
        ),
        enquiry = props.details.listMetadata.enquiryurl && (
            <p className="card__tag">
                <span className="fas fa-fw fa-edit icon" aria-hidden="true" />{' '}
                <span>Enquiries:</span>{' '}
                <a href={props.details.listMetadata.enquiryurl[0]}>
                    {props.details.listMetadata.enquirylabel[0]}
                </a>
            </p>
        );

    return (
        <li className="card card--contact">
            <div className="card__details">
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    {jobtitle}
                    {school}
                    {department}
                    {room}
                    {email}
                    {telephone}
                    {enquiry}
                </div>
            </div>
        </li>
    );
}

Finder__Results__Contact.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Contact;
