import React from 'react';
import PropTypes from 'prop-types';
import { formatReactDate } from '../../../../util';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__News(props) {
    const formattedDate = formatReactDate(new Date(props.details.listMetadata.d[0])),
        dateString = props.details.listMetadata.d && (
            <p className="card__type">{formattedDate}</p>
        ),
        hashtags = props.details.listMetadata.hashtagtext && (
            <p className="card__tag">
                <span
                    className="fad fa-fw fa-hashtag icon"
                    aria-hidden="true"
                />{' '}
                <span>
                    Hashtag
                    {props.details.listMetadata.hashtagtext.length > 1 &&
                        's'}
                    :
                </span>{' '}
                <span>
                    {props.details.listMetadata.hashtagtext.join(', ')}
                </span>
            </p>
        ),
        thumbnail =
            props.details.listMetadata.subject &&
            props.details.listMetadata.thumbnail ? (
                <img
                    src={props.details.listMetadata.thumbnail[0]}
                    alt=""
                    className="card__thumbnail"
                />
            ) : props.details.listMetadata.thumbnail ? (
                <picture>
                    <source
                        srcSet={props.details.listMetadata.thumbnailTiny[0]}
                        media="(max-width: 375px)"
                    />
                    <source
                        srcSet={props.details.listMetadata.thumbnailMax[0]}
                        media="(min-width: 3840px)"
                    />
                    <img
                        src={props.details.listMetadata.thumbnailMobile[0]}
                        alt={props.details.title}
                    />
                </picture>
            ) : null;

    return (
        <li className="card card--news">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                {thumbnail}
                <div className="card__details__text">
                    {dateString}
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__description">
                        {props.details.listMetadata.c && props.details.listMetadata.c[0]}
                    </p>
                    {hashtags}
                </div>
            </a>
        </li>
    );
}

Finder__Results__News.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__News;
